<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div v-if="newadd" class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>Add Rooms</h1>
          </div>
          <div class="col-sm-3 ml-auto">
            <base-button type="primary" @click="newadd = !newadd">Back</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-sm-6 offset-sm-3">
            <card style="min-height: 200px;">
              <form @submit.prevent>
                <div>
                  <base-input
                    label="Enter Room Name"
                    type="text"
                    placeholder="Enter Name"
                    v-model="newroom.name"
                  >
                  </base-input>
                  <base-input
                    label="Enter Room Floor"
                    type="number"
                    placeholder="Enter Floor"
                    v-model="newroom.floor"
                  >
                  </base-input>
                  <base-input
                    label="Choose Room Type"
                  >
                    <el-select
                      class="select-primary"
                      size="large"
                      placeholder="Single Select"
                      v-model="newroom.type_id"
                    >
                      <el-option
                        v-for="option in roomtypes"
                        class="select-primary"
                        :value="option.id"
                        :label="option.name"
                        :key="option.id"
                      >
                      </el-option>
                    </el-select>
                  </base-input>
                  <base-button class="mt-3" native-type="submit" type="primary" @click="createnew"
                  >Submit</base-button
                  >
                </div>
              </form>
            </card>
          </div>
        </div>
      </div>
      <div v-else class="padd">
        <div class="row">
          <div class="col-md-12 col-lg-8 mr-auto">
            <h1>All Rooms</h1>
          </div>
          <div class="col-md-3 col-lg-2 ml-auto">
            <base-button type="primary" @click="updateroomtypes">Update Roomtypes</base-button>
          </div>
          <div class="col-md-3 col-lg-2 ml-auto">
            <base-button type="primary" @click="switchtoaddnew">Add New</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <template v-for="item in this.dataformatted">
            <div v-bind:key="item.id" class="col-md-12 col-lg-6">
              <card style="min-height: 200px;">
                <div class="ml-auto mr-auto" style="text-align: justify;padding:40px;">
                  <tabs type="primary" vertical class="row">
                    <tab-pane label="Data">
                      <h1 class="card-subtitle" style="margin-top: 5px;">Name: &nbsp;{{item['name']}}</h1>
                      <h2 class="card-title" style="padding-top: 20px;padding-left: 0px;">Price: &nbsp;Rs. {{item['roomtype']['price']}}</h2>
                      <h2 class="card-title" style="padding-top: 20px;padding-left: 0px;">Type: &nbsp;{{item['roomtype']['name']}}</h2>
                      <h2 class="card-title" style="padding-top: 20px;padding-left: 0px;">Floor: &nbsp;{{item['floor']}}</h2>
                    </tab-pane>
                    <tab-pane label="Status">
                      <h1 class="card-subtitle" style="margin-top: 5px;">Booked: &nbsp;
                        <base-switch
                          v-model="item.booked"
                          type="primary"
                          on-text="ON"
                          off-text="OFF"
                          style="margin-top:10px;"
                        ></base-switch></h1>
                      <h2 class="card-title" style="padding-top: 20px;padding-left: 0px;">Disabled: &nbsp;
                        <base-switch
                          v-model="item.disabled"
                          type="primary"
                          on-text="ON"
                          off-text="OFF"
                          style="margin-top:10px;"
                        ></base-switch></h2>
                      <base-button type="primary" @click="updateitem(item)">Update</base-button>
                    </tab-pane>
                    <tab-pane label="Update">
                      <form @submit.prevent>
                        <div>
                          <base-input
                            label="Enter Room Name"
                            type="text"
                            placeholder="Enter Name"
                            v-model="item.name"
                          >
                          </base-input>
                          <base-input
                            label="Enter Room Floor"
                            type="number"
                            placeholder="Enter Floor"
                            v-model="item.floor"
                          >
                          </base-input>
                          <base-input
                            label="Choose Room Type"
                          >
                            <el-select
                              class="select-primary"
                              size="large"
                              placeholder="Single Select"
                              v-model="item.type_id"
                            >
                              <el-option
                                v-for="option in roomtypes"
                                class="select-primary"
                                :value="option.id"
                                :label="option.name"
                                :key="option.id"
                              >
                              </el-option>
                            </el-select>
                          </base-input>
                          <base-button class="mt-3" native-type="submit" type="primary" @click="updateitem(item)"
                          >Submit</base-button
                          >
                        </div>
                      </form>
                    </tab-pane>
                  </tabs>
                </div>
                <div class="card-footer">
                  <hr style="background-color:rgba(255, 255, 255, 0.8);"/>
                  <div class="row">
                    <div class="col-md-3 mr-auto">
                      <h5>ID:  &nbsp;{{item.id}}</h5>
                    </div>
                    <div class="col-md-7 ml-auto">
                      <h5>Created At: &nbsp; {{item.createdAt}}</h5>
                    </div>
                  </div>
                </div>
              </card>
            </div>
          </template>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import swal from 'sweetalert2';
  import { Select, Option } from 'element-ui';
  import { BaseSwitch, TabPane, Tabs } from 'src/components';
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "rooms",
    components: {
      BaseSwitch,
      TabPane,
      [Option.name]: Option,
      [Select.name]: Select,
      Tabs,
      Loading
    },
    data: function () {
      return {
        isLoading: true,
        roomtypes: {},
        newadd: false,
        newroom: {name: '', floor: '', type_id: 0 },
        rooms: {}
      }
    },
    computed: {
      dataformatted () {
        let formatted = this.rooms;
        formatted.forEach((element) => {
          element.booked = element.booked ? true : false;
          element.disabled = element.disabled ? true : false;
        });
        return formatted;
      }
    },
    methods: {
      updateroomtypes() {
        this.isLoading = true;
        this.$axios.get(this.$backend+'/api/roomtypes')
          .then(res => {
            this.roomtypes = res.data;
            this.isLoading = false;
            swal({
              title: `Success!`,
              text: 'Room Types updated successfully!',
              type: 'success',
              timer: 1000,
              showConfirmButton: false
            });
          }).catch(err => {
            swal({
              title: `Error!`,
              text: 'Room Types could not be updated!',
              type: 'error',
              timer: 1500,
              showConfirmButton: false
            }).then(rr => {
              this.isLoading = false;
            });
        });
      },
      switchtoaddnew() {
        this.isLoading = true;
        this.$axios.get(this.$backend+'/api/roomtypes')
          .then(res => {
            this.roomtypes = res.data;
            this.isLoading = false;
            this.newadd = true;
          }).catch(err => {
          this.isLoading = false;
        });
      },
      updateitem (payload) {
        let updatert = {
          id: payload.id,
          name: payload.name,
          booked: payload.booked,
          disabled: payload.disabled,
          floor: payload.floor,
          type_id: payload.type_id
        }
        this.isLoading = true;
        this.$axios.put(this.$backend+'/api/admin/room', updatert)
          .then(res => {
            this.$axios.get(this.$backend+'/api/rooms')
              .then(res => {
                this.rooms = res.data;
                this.isLoading = false;
                swal({
                  title: `Success!`,
                  text: 'The Room was updated successfully!',
                  type: 'success',
                  timer: 2000,
                  showConfirmButton: false
                });
              });
          }).catch(err => {
          this.isLoading = false;
          swal({
            title: `Error!`,
            text: 'The Room could not be updated!',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success btn-fill',
            type: 'error'
          });
        });
      },
      createnew () {
        this.isLoading = true;
        this.newadd = false;
        this.$axios.post(this.$backend+'/api/admin/room', this.newroom)
          .then(res => {
            this.$axios.get(this.$backend+'/api/rooms')
              .then(res => {
                this.rooms = res.data;
                this.isLoading = false;
                swal({
                  title: `Success!`,
                  text: 'The Room Type was added successfully!',
                  type: 'success',
                  timer: 2000,
                  showConfirmButton: false
                });
              });
          }).catch(err => {
          this.isLoading = false;
          swal({
            title: `Error!`,
            text: 'The Room Type could not be added!',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success btn-fill',
            type: 'error'
          });
        });
      }
    },
    mounted() {
      this.$axios.get(this.$backend+'/api/rooms')
        .then(res => {
          this.rooms = res.data;
          this.$axios.get(this.$backend+'/api/roomtypes')
            .then(res => {
              this.roomtypes = res.data;
              this.isLoading = false;
            }).catch(err => {
            this.isLoading = false;
          });
        }).catch(err => {
        this.isLoading = false;
      });
    }
  }
</script>

<style scoped>
  .padd {
    padding-left: 40px;
    padding-right: 60px;
    padding-top: 15px;
    width: auto;
  }
</style>
