<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div class="row padd">
        <div class="col-sm-8 mr-auto">
          <h1>Rooms Status</h1>
        </div>
      </div>
      <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
      <div class="row">
        <div class="col-md-12">
        <card body-classes="table-full-width">
          <h4 slot="header" class="card-title">Legend<br/><br/><span style="background-color: #1d8cf8;">Booked</span>&nbsp;<span style="background-color: #ff8d72;">Disabled</span>&nbsp;<span style="background-color: #fd5d93;">Booked and Disabled</span>&nbsp;&nbsp;<span>Available</span></h4>
          <el-table :row-class-name="tableRowClassName" :data="roomsdata">
            <el-table-column
              min-width="150"
              sortable
              label="ID"
              property="id"
            ></el-table-column>
            <el-table-column
              min-width="150"
              sortable
              label="Name"
              property="name"
            ></el-table-column>
            <el-table-column
              min-width="150"
              sortable
              label="Floor"
              property="floor"
            ></el-table-column>
            <el-table-column
              min-width="150"
              sortable
              label="Room Type"
              property="roomtypename"
            ></el-table-column>
          </el-table>
        </card>
      </div>
      </div>
    </template>
  </div>
</template>

<script>
  import { Table, TableColumn } from 'element-ui';
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "currentrooms",
    components: {
      Loading,
      [Table.name]: Table,
      [TableColumn.name]: TableColumn
    },
    data: function () {
      return {
        isLoading: false,
        rooms: ''
      }
    },
    computed: {
      roomsdata () {
        let formatted = this.rooms;
        let x = 0;
        for (x=0; x<formatted.length; x++)
        {
          formatted[x]['roomtypename'] = formatted[x]['roomtype']['name'];
        }
        // formatted.forEach((element) => {
        //   element['roomtypename'] = element['roomtype']['name'];
        // });
        return formatted;
      }
    },
    mounted() {
      this.$axios.get(this.$backend+'/api/rooms')
        .then(res => {
          this.rooms = res.data;
          this.isLoading = false;
        }).catch(err => {
          this.isLoading = false;
      });
    },
    methods: {
      tableRowClassName({ row }) {
        if (row['disabled'] === 1 && row['booked'] === 1) {
          return 'table-danger';
        } else if (row['disabled'] === 1) {
          return 'table-warning';
        } else if (row['booked'] === 1) {
          return 'table-info';
        }
        return '';
      }
    }
  }
</script>

<style scoped>
  .padd {
    padding-left: 40px;
    padding-right: 60px;
    padding-top: 15px;
    width: auto;
  }
</style>
