<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div v-if="newadd" class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>Add Service Types</h1>
          </div>
          <div class="col-sm-3 ml-auto">
            <base-button type="primary" @click="newadd = !newadd">Back</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-sm-6 offset-sm-3">
            <card style="min-height: 200px;">
              <form @submit.prevent>
                <div>
                  <base-input
                    label="Enter Service Type Name"
                    type="text"
                    placeholder="Enter Name"
                    v-model="newservicetype.name"
                  >
                  </base-input>
                  <base-button class="mt-3" native-type="submit" type="primary" @click="createnew"
                  >Submit</base-button
                  >
                </div>
              </form>
            </card>
          </div>
        </div>
      </div>
      <div v-else class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>All Service Types</h1>
          </div>
          <div class="col-sm-3 ml-auto">
            <base-button type="primary" @click="newadd = !newadd">Add New</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <template v-for="item in this.servicetypes">
            <div v-bind:key="item.id" class="col-md-6 col-lg-4">
              <card style="min-height: 200px;">
                <div class="ml-auto mr-auto" style="text-align: center;padding:40px;">
                  <h2 class="card-title">Name</h2>
                  <h1 class="card-subtitle" style="margin-top: 15px;margin-left: -5px;">{{item['name']}}</h1>
                </div>
                <div class="card-footer">
                  <hr style="background-color:rgba(255, 255, 255, 0.8);"/>
                  <div class="row">
                    <div class="col-md-3 mr-auto">
                      <h5>ID:  &nbsp;{{item.id}}</h5>
                    </div>
                    <div class="col-md-8 ml-auto">
                      <h5>Created At: &nbsp; {{item.createdAt}}</h5>
                    </div>
                  </div>
                </div>
              </card>
            </div>
          </template>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import swal from 'sweetalert2';
  import { TabPane, Tabs } from 'src/components';
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "servicetypes",
    components: {
      TabPane,
      Tabs,
      Loading
    },
    data: function () {
      return {
        isLoading: true,
        servicetypes: {},
        newadd: false,
        newservicetype: {name: ''}
      }
    },
    methods: {
      createnew () {
        this.isLoading = true;
        this.newadd = false;
        this.$axios.post(this.$backend+'/api/admin/servicetype', this.newservicetype)
          .then(res => {
            this.$axios.get(this.$backend+'/api/customer/servicetypes')
              .then(res => {
                this.servicetypes = res.data;
                this.isLoading = false;
                swal({
                  title: `Success!`,
                  text: 'The Service Type was added successfully!',
                  type: 'success',
                  timer: 2000,
                  showConfirmButton: false
                });
              });
          }).catch(err => {
            this.isLoading = false;
            swal({
              title: `Error!`,
              text: 'The Service Type could not be added!',
              buttonsStyling: false,
              confirmButtonClass: 'btn btn-success btn-fill',
              type: 'error'
            });
        });
      }
    },
    mounted() {
      this.$axios.get(this.$backend+'/api/customer/servicetypes')
        .then(res => {
          this.servicetypes = res.data;
          this.isLoading = false;
        });
    }
  }
</script>

<style scoped>
.padd {
  padding-left: 40px;
  padding-right: 60px;
  padding-top: 15px;
  width: auto;
}
</style>
