<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>Employee Info</h1>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-sm-12 col-md-8 col-xl-6" style="margin-bottom: 45px;">
            <card type="testimonial" header-classes="card-header-avatar">
              <a slot="header" href="#pablo">
                <img class="img img-raised" src="img/james.jpg" alt="Card image" />
              </a>
              <h2 class="card-title">{{ this.employee.fname }} &nbsp;{{ (this.employee.lname === '' || this.employee.lname === null) ? null : this.employee.lname }}</h2>
              <a class="card-title" :href="'tel:+'+this.employee.phone">Phone: {{ this.employee.phone }}</a>
              <br/><br/><h4 class="card-title">Designation: {{ this.employee.designation }}</h4>
              <h4 class="card-title">Service Type: {{ this.employee.servicetype.name }}</h4>
              <div class="card-footer" style="padding-bottom: 0px; margin-bottom: 0px;">
                <hr style="background-color:rgba(255, 255, 255, 0.8);"/>
                <div class="row">
                  <div class="col-sm-3 col-md-3 mr-auto">
                    <h5>ID:  &nbsp;{{ this.employee.id }}</h5>
                  </div>
                  <div class="col-sm-8 col-md-8 ml-auto">
                    <h5>CreatedAt:  &nbsp;{{ this.employee.createdAt }}</h5>
                  </div>
                </div>
              </div>
            </card>
          </div>
        </div>
      </div>
      <div v-if="employee.servicerequests.length !== 0" class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>Service Requests</h1>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-sm-12" style="margin-bottom: 45px;">
            <card type="testimonial" header-classes="card-header-avatar">
              <el-table :data="employee.servicerequests">
                <el-table-column
                  min-width="50"
                  sortable
                  label="ID"
                  property="id"
                ></el-table-column>
                <el-table-column
                  min-width="250"
                  sortable
                  label="Description"
                  property="description"
                ></el-table-column>
                <el-table-column
                  min-width="150"
                  sortable
                  label="Preferred Time"
                  property="preferredtime"
                ></el-table-column>
                <el-table-column :min-width="135" align="right" label="Actions">
                  <div slot-scope="props">
                    <base-button
                      @click.native="gotobooking(props.$index, props.row)"
                      class="edit btn-link"
                      type="info"
                      style="color: #fff!important;"
                      size="sm"
                      icon
                    >
                      <i class="tim-icons icon-paper"></i>
                    </base-button>
                  </div>
                </el-table-column>
              </el-table>
            </card>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import { Table, TableColumn } from 'element-ui';
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "employee",
    components: {
      Loading,
      [Table.name]: Table,
      [TableColumn.name]: TableColumn
    },
    data: function () {
      return {
        isLoading: false,
        employee: ''
      }
    },
    methods: {
      gotobooking (index, row) {
        this.$router.push({ name: 'Booking', params:{id: row.booking_id}})
      }
    },
    mounted() {
      this.$axios.post(this.$backend+'/api/admin/getemployee', this.$route.params)
        .then(res => {
          this.employee = res.data;
          this.isLoading = false;
        }).catch(err => {
          console.log(err);
      });
    }
  }
</script>

