<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>All Service Requests</h1>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <template v-for="item in this.typeservicerequests">
            <div v-bind:key="item.id" class="col-lg-12 col-xl-6">
              <card style="min-height: 200px;">
                <div class="ml-auto mr-auto" style="text-align: left;padding:40px;">
                  <h2 class="card-subtitle" style="margin-top: 15px;">Name: {{item['booking']['customer']['fname']}}</h2>
                  <h2 class="card-subtitle" style="margin-top: 15px;">Room/Floor: {{item['booking']['room']['name']+' // '+item['booking']['room']['floor']}}</h2>
                  <h2 class="card-subtitle" style="margin-top: 15px;">Service Type: {{item['servicetype']['name']}}</h2>
                  <h2 class="card-subtitle" style="margin-top: 15px;">Preferred Time: {{item['preferredtime']}}</h2>
                  <h2 class="card-subtitle" style="margin-top: 15px;">Description:</h2>
                  <span class="card-subtitle" style="margin-top: 15px;">{{item['description']}}</span>
                </div>
                <div class="card-footer">
                  <hr style="background-color:rgba(255, 255, 255, 0.8);"/>
                  <div class="row">
                    <div class="col-md-3 mr-auto">
                      <h5>ID:  &nbsp;{{item.id}}</h5>
                    </div>
                    <div class="col-md-6 ml-auto mr-auto">
                      <h5>Created At: &nbsp; {{item.createdAt}}</h5>
                    </div>
                    <div class="col-md-2 ml-auto">
                      <base-button type="primary btn-sm" style="margin: 0px;" @click="acceptServiceRequest(item.id)">Accept</base-button>
                    </div>
                  </div>
                </div>
              </card>
            </div>
          </template>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import swal from 'sweetalert2';
  import { TabPane, Tabs } from 'src/components';
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "availableservicerequests",
    components: {
      TabPane,
      Tabs,
      Loading
    },
    data: function () {
      return {
        isLoading: true,
        servicerequests: {}
      }
    },
    methods: {
      acceptServiceRequest (id) {
        swal({
          title: 'Are you sure?',
          text: 'You will not be able to revert this action!',
          type: 'warning',
          showCancelButton: true,
          confirmButtonText: 'Yes, accept it!',
          cancelButtonText: 'Cancel',
          confirmButtonClass: 'btn btn-success btn-fill',
          cancelButtonClass: 'btn btn-danger btn-fill',
          buttonsStyling: false
        }).then( (res) => {
            if (res.value) {
              let sendData = {
                assigned_to: this.$store.state.id,
                id: id
              };
              this.$axios.post(this.$backend+'/api/employees/acceptservicerequests', sendData)
                .then(res => {
                  this.$axios.get(this.$backend+'/api/employees/availableservicerequests')
                    .then(res => {
                      this.servicerequests = res.data;
                      console.log(this.servicerequests)
                      this.isLoading = false;
                    }).catch( err => {
                    console.log(err);
                  });
                }).catch( err => {
                  console.log(err);
                  swal({
                    title: 'Server Issue!',
                    text: 'Service Request Not Accepted.',
                    type: 'error',
                    confirmButtonClass: 'btn btn-success btn-fill',
                    buttonsStyling: false
                  });
              });
              swal({
                title: 'Success!',
                text: 'Service Request has been accepted successfully.',
                type: 'success',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              });
            } else {
              swal({
                title: 'Cancelled!',
                text: 'Service Request Not Accepted.',
                type: 'error',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              });
            }
          });
      }
    },
    computed: {
      typeservicerequests () {
        let formatted = this.servicerequests;
        let x = 0;
        let newf = [];
        for (x=0; x<formatted.length; x++)
        {
          if (this.$store.state.emp_profile.service_id === formatted[x].servicetype_id)
          {
            newf.push(formatted[x])
          }
        }
        return newf;
      }
    },
    mounted() {
      this.$axios.get(this.$backend+'/api/employees/availableservicerequests')
        .then(res => {
          this.servicerequests = res.data;
          console.log(this.servicerequests)
          this.isLoading = false;
        }).catch( err => {
        console.log(err);
      });
    }
  }
</script>

<style scoped>
  .padd {
    padding-left: 40px;
    padding-right: 60px;
    padding-top: 15px;
    width: auto;
  }
</style>
