<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>Booking Info</h1>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-sm-12 col-md-10 col-xl-8 ml-auto mr-auto" style="margin-bottom: 45px;">
            <card style="padding: 20px;">

              <tabs type="primary" vertical class="row">
                <tab-pane label="Info">
                  <h4>ID: {{this.booking.id}}</h4>
                  <h4>Start Date: {{this.booking.startdate}}</h4>
                  <h4>End Date: {{this.booking.enddate}}</h4>
                  <h4>Num Guests: {{this.booking.numguests}}</h4>
                </tab-pane>

                <tab-pane label="Customer">
                  <h4>Name: {{this.booking.customer.fname+' '+this.booking.customer.lname}}</h4>
                  <h4><router-link :to="{ name: 'Customer_emp', params:{id: this.booking.customer.id}}">Open Profile</router-link></h4>
                </tab-pane>

                <tab-pane label="Status">
                  <h4>Cancelled: {{this.booking.cancelled===1?'Yes':'No'}}</h4>
                  <h4>CheckedIn: {{this.booking.checkedin===1?'Yes':'No'}}</h4>
                  <h4>CheckedOut: {{this.booking.checkedout===1?'Yes':'No'}}</h4>
                  <h4>Paid: {{this.booking.paid===1?'Yes':'No'}}</h4>
                  <h4>Created At: {{this.booking.createdAt}}</h4>
                </tab-pane>

                <tab-pane v-if="booking.bill!==null" label="Bill">
                  <h4>Amount: Rs. {{this.booking.bill.amount}}</h4>
                  <h4 v-if="booking.bill.payment_id !== null">Payment ID: {{this.booking.bill.payment_id}}</h4>
                  <h4>Payment Date: {{this.booking.bill.createdAt}}</h4>
                  <h4>Paid: {{this.booking.paid===1?'Yes':'No'}}</h4>
                  <a :href="this.$backend+'/'+booking.bill.pdf_download_url">Download Bill</a>
                </tab-pane>
                <tab-pane v-if="booking.guests.length!==0" label="Guests">
                  <template v-for="(item, index) in booking.guests">
                    <h2 v-bind:key="item.id">Guest&nbsp;{{index + 1}}&nbsp;=></h2>
                    <h4>Name: {{item.name}}&nbsp;&nbsp;/&nbsp;&nbsp;ID: {{item.idproof}}</h4>
                    <br/>
                  </template>
                </tab-pane>
              </tabs>
            </card>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import { Table, TableColumn } from 'element-ui';
  import { BaseSwitch, TabPane, Tabs } from 'src/components';
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "booking",
    components: {
      Loading,
      BaseSwitch,
      TabPane,
      Tabs,
      [Table.name]: Table,
      [TableColumn.name]: TableColumn
    },
    data: function () {
      return {
        isLoading: false,
        booking: ''
      }
    },
    methods: {
      gotobooking (index, row) {
        this.$router.push('/admin/employee/' + row.assigned_to)
      }
    },
    mounted() {
      this.$axios.post(this.$backend+'/api/employees/booking', this.$route.params)
        .then(res => {
          this.booking = res.data;
          this.isLoading = false;
        }).catch(err => {
        console.log(err);
      });
    }
  }
</script>

