<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div class="row">
        <div class="col-md-8"><edit-profile-form :user="profile" @reloadDataa="updateProfile"> </edit-profile-form></div>
        <div class="col-md-4"><user-card :user="profile"> </user-card></div>
      </div>
    </template>
  </div>
</template>

<script>
  import EditProfileForm from './UserProfile/EditProfileForm.vue';
  import UserCard from './UserProfile/UserCard.vue';
  import swal from "sweetalert2";
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "profile",
    components: {
      EditProfileForm,
      UserCard,
      Loading
    },
    methods: {
      updateProfile () {
        this.$axios.post(this.$backend+'/api/customer/getprofile', this.sd)
          .then(res => {
            console.log(res.data)
            this.profile = res.data;
            this.isLoading = false;
            swal({
              title: `Success!`,
              text: 'Profile updated successfully!',
              type: 'success',
              timer: 1500,
              showConfirmButton: false
            });
          }).catch(err => {
          this.isLoading = false;
        });
      }
    },
    mounted() {
      this.$axios.post(this.$backend+'/api/customer/getprofile', this.sd)
        .then(res => {
          this.profile = res.data;
          this.isLoading = false;
        }).catch(err => {
        this.isLoading = false;
      });
    },
    data: function () {
      return {
        isLoading: false,
        profile: {},
        sd: {
          id: this.$store.state.userid
        }
      }
    },
  }
</script>

