<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div v-if="checkingin">
        <div class="row padd">
          <div class="col-sm-8 mr-auto">
            <h1>Check In: {{this.selected.customer.fname}}</h1>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-md-12">
            <card card-body-classes="table-full-width">
              <h4 v-if="selected.numguests - guests.length !== 0" slot="header" class="card-title">Enter {{this.selected.numguests - this.guests.length}} more Guests to proceed.</h4>
              <h4 v-else slot="header" class="card-title">Click Check In to proceed.</h4>
              <el-table :data="guests">
                <el-table-column
                  min-width="150"
                  label="Name"
                  property="name"
                ></el-table-column>
                <el-table-column
                  min-width="150"
                  label="ID Proof"
                  property="idproof"
                ></el-table-column>
                <el-table-column :min-width="135" align="right" label="Actions">
                  <div slot-scope="props">
                    <base-button
                      @click.native="handleDelete(props.$index, props.row)"
                      class="remove btn-link"
                      type="danger"
                      size="sm"
                      icon
                    >
                      <i class="tim-icons icon-simple-remove"></i>
                    </base-button>
                  </div>
                </el-table-column>
              </el-table>
            </card>
          </div>
        </div>
        <div class="row" v-if="selected.numguests - guests.length !== 0">
          <div class="col-sm-8 mr-auto ml-auto">
            <card class="stacked-form" title="Stacked Form">
              <h4 slot="header" class="card-title">Guest Form</h4>
              <form @submit.prevent>
                <div>
                  <base-input
                    label="Enter Guest Name"
                    type="text"
                    placeholder="Enter Guest Name"
                    v-model="guestadd.name"
                  >
                  </base-input>
                  <base-input v-model="guestadd.idproof" label="Enter Guest IDProof" type="text" placeholder="Enter Guest IDProof">
                  </base-input>
                  <base-button @click="addguest" class="mt-3" native-type="submit" type="primary"
                  >Submit</base-button
                  >
                </div>
              </form>
            </card>
          </div>
        </div>
        <div class="row" v-else>
          <div class="col-sm-8 mr-auto ml-auto">
            <card class="stacked-form" title="Stacked Form">
              <base-button @click="checkinfinal" class="mt-3" native-type="submit" type="primary"
              >Check In</base-button>
            </card>
          </div>
        </div>
      </div>
      <div v-else>
        <div class="row padd">
          <div class="col-sm-8 mr-auto">
            <h1>Upcoming Bookings</h1>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row mt-5">
          <div class="col-12">
            <card card-body-classes="table-full-width">
              <h4 slot="header" class="card-title">Legend<br/><br/><span style="background-color: #1d8cf8;">Upcoming</span>&nbsp;&nbsp;<span>Booked</span></h4>
              <div>
                <div
                  class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap"
                >
                  <el-select
                    class="select-primary mb-3 pagination-select"
                    v-model="pagination.perPage"
                    placeholder="Per page"
                  >
                    <el-option
                      class="select-primary"
                      v-for="item in pagination.perPageOptions"
                      :key="item"
                      :label="item"
                      :value="item"
                    >
                    </el-option>
                  </el-select>

                  <base-input>
                    <el-input
                      type="search"
                      class="mb-3 search-input"
                      clearable
                      prefix-icon="el-icon-search"
                      placeholder="Search records"
                      v-model="searchQuery"
                      aria-controls="datatables"
                    >
                    </el-input>
                  </base-input>
                </div>
                <el-table :data="queriedData" :row-class-name="tableRowClassName">
                  <el-table-column
                    v-for="column in tableColumns"
                    :key="column.label"
                    :min-width="column.minWidth"
                    :prop="column.prop"
                    :label="column.label"
                  >
                  </el-table-column>
                  <el-table-column :min-width="135" align="right" label="Actions">
                    <div slot-scope="props">
                      <base-button
                        @click.native="handleLike(props.$index, props.row)"
                        class="like btn-link"
                        type="info"
                        style="color: #fff!important;"
                        size="sm"
                        icon
                      >
                        <i class="tim-icons icon-single-02"></i>
                      </base-button>
                    </div>
                  </el-table-column>
                </el-table>
              </div>
              <div
                slot="footer"
                class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap"
              >
                <div class="">
                  <p class="card-category">
                    Showing {{ from + 1 }} to {{ to }} of {{ total }} entries
                  </p>
                </div>
                <base-pagination
                  class="pagination-no-border"
                  v-model="pagination.currentPage"
                  :per-page="pagination.perPage"
                  :total="total"
                >
                </base-pagination>
              </div>
            </card>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import { Table, TableColumn, Select, Option } from 'element-ui';
  import { BasePagination } from 'src/components';
  import Fuse from 'fuse.js';
  import swal from "sweetalert2";
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "checkin",
    components: {
      Loading,
      BasePagination,
      [Select.name]: Select,
      [Option.name]: Option,
      [Table.name]: Table,
      [TableColumn.name]: TableColumn
    },
    computed: {
      bookingsformatted () {
        let formatted = this.bookings;
        let x = 0;
        let newf = [];
        for (x=0; x<formatted.length; x++)
        {
          var nowDate2 = new Date();
          var nowDate = new Date(formatted[x]['startdate']);
          function daysBetween(startDate, endDate) {
            var millisecondsPerDay = 24 * 60 * 60 * 1000;
            return (endDate - startDate) / millisecondsPerDay;
          }
          formatted[x]['isupcoming'] = (daysBetween(nowDate2, nowDate) >=0 && daysBetween(nowDate2, nowDate) <= 7) ? true : false;
          console.log(daysBetween(nowDate2, nowDate))
          console.log(formatted[x]['isupcoming'])
          nowDate = new Date(formatted[x]['startdate']);
          var date = nowDate.getFullYear()+' - '+(nowDate.getMonth()+1)+' - '+nowDate.getDate();
          formatted[x]['startdate'] = date;
          nowDate = new Date(formatted[x]['enddate']);
          date = nowDate.getFullYear()+' - '+(nowDate.getMonth()+1)+' - '+nowDate.getDate();
          formatted[x]['enddate'] = date;
          nowDate = new Date(formatted[x]['createdAt']);
          date = nowDate.getFullYear()+' - '+(nowDate.getMonth()+1)+' - '+nowDate.getDate();
          formatted[x]['createdAt'] = date;
          formatted[x]['roominfo'] = formatted[x]['room']['name'] + ' / ' + formatted[x]['room']['floor'];
          formatted[x]['custname'] = formatted[x]['customer']['fname'] + ' ' + formatted[x]['customer']['lname'];
          nowDate = new Date(formatted[x]['startdate']);
          if(formatted[x]['checkedin']===0 && formatted[x]['checkedout']===0 && formatted[x]['cancelled']===0 )
          {
            newf.push(formatted[x])
          }
        }
        return newf;
      },
      queriedData() {
        let result = this.bookingsformatted;
        if (this.searchedData.length > 0) {
          result = this.searchedData;
        }
        return result.slice(this.from, this.to);
      },
      to() {
        let highBound = this.from + this.pagination.perPage;
        if (this.total < highBound) {
          highBound = this.total;
        }
        return highBound;
      },
      from() {
        return this.pagination.perPage * (this.pagination.currentPage - 1);
      },
      total() {
        return this.searchedData.length > 0
          ? this.searchedData.length
          : this.bookingsformatted.length;
      }
    },
    data: function () {
      return {
        guestadd: {
          name: '',
          idproof: ''
        },
        checkingin: false,
        selected: '',
        guests: [],
        isLoading: true,
        bookings: '',
        pagination: {
          perPage: 10,
          currentPage: 1,
          perPageOptions: [10, 25, 50],
          total: 0
        },
        searchQuery: '',
        propsToSearch: ['id', 'custname', 'startdate', 'createdAt'],
        tableColumns: [
          {
            prop: 'id',
            label: 'ID',
            minWidth: 50
          },
          {
            prop: 'custname',
            label: 'Customer',
            minWidth: 120
          },
          {
            prop: 'startdate',
            label: 'From',
            minWidth: 120
          },
          {
            prop: 'enddate',
            label: 'To',
            minWidth: 120
          },
          {
            prop: 'createdAt',
            label: 'Booking Date',
            minWidth: 120
          },
          {
            prop: 'numguests',
            label: 'Guests',
            minWidth: 80
          },
          {
            prop: 'roominfo',
            label: 'Room / Floor',
            minWidth: 110
          }
        ],
        searchedData: [],
        fuseSearch: null
      }
    },
    mounted() {
      this.$axios.get(this.$backend+'/api/employees/bookings')
        .then(res => {
          this.bookings = res.data;
          this.isLoading = false;
          this.fuseSearch = new Fuse(this.bookingsformatted, {
            keys: ['id', 'custname', 'startdate', 'createdAt'],
            threshold: 0.3
          });
        }).catch(err => {
        this.isLoading = false;
      });
    },
    methods: {
      checkinfinal () {
        let pp = {
          id: this.selected.id,
          guests: this.guests
        }
        this.isLoading = true;
        this.$axios.post(this.$backend+'/api/employees/checkin', pp)
          .then(res => {
            swal({
              title: 'Success!',
              text: `${this.selected.custname} Checked In`,
              type: 'success',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false
            }).then(res => {
              this.$axios.get(this.$backend + '/api/employees/bookings')
                .then(res => {
                  this.bookings = res.data;
                  this.isLoading = false;
                  this.selected = '';
                  this.guests = [];
                  this.checkingin = false;
                  this.fuseSearch = new Fuse(this.bookingsformatted, {
                    keys: ['id', 'custname', 'startdate', 'createdAt'],
                    threshold: 0.3
                  });
                }).catch(err => {
                  this.selected = '';
                  this.guests = [];
                  this.checkingin = false;
                  this.isLoading = false;
              });
            });
          }).catch(err => {
            this.selected = '';
            this.guests = [];
            this.checkingin = false;
            this.isLoading = false;
        });
      },
      handleDelete(index, row) {
        swal({
          title: 'Are you sure?',
          text: `You won't be able to revert this!`,
          type: 'warning',
          showCancelButton: true,
          confirmButtonClass: 'btn btn-success btn-fill',
          cancelButtonClass: 'btn btn-danger btn-fill',
          confirmButtonText: 'Yes, delete it!',
          buttonsStyling: false
        }).then(result => {
          if (result.value) {
            this.deleteRow(row);
            swal({
              title: 'Deleted!',
              text: `You deleted ${row.name}`,
              type: 'success',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false
            });
          }
        });
      },
      deleteRow(row) {
        let indexToDelete = this.guests.findIndex(
          tableRow => tableRow.name === row.name
        );
        if (indexToDelete >= 0) {
          this.guests.splice(indexToDelete, 1);
        }
      },
      addguest () {
        this.guests.push(this.guestadd);
        this.guestadd = {};
      },
      tableRowClassName({ row }) {
        if (row['cancelled'] === 1) {
          return 'table-danger';
        } else if (row['checkedin'] === 1 && row['paid'] !== 1) {
          return 'table-warning';
        } else if (row['checkedin'] === 1 && row['paid'] === 1) {
          return 'table-success';
        } else if (row['isupcoming'] === true) {
          return 'table-info';
        }
        return '';
      },
      handleLike(index, row) {
        this.selected = row;
        this.checkingin = true;
      }
    },
    watch: {
      /**
       * Searches through the table data by a given query.
       * NOTE: If you have a lot of data, it's recommended to do the search on the Server Side and only display the results here.
       * @param value of the query
       */
      searchQuery(value) {
        let result = this.bookingsformatted;
        if (value !== '') {
          result = this.fuseSearch.search(this.searchQuery);
        }
        this.searchedData = result;
      }
    }
  }
</script>

<style scoped>
  .pagination-select,
  .search-input {
    width: 200px;
  }
  .padd {
    padding-left: 40px;
    padding-right: 60px;
    padding-top: 15px;
    width: auto;
  }
</style>
