<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div v-if="newadd" class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>Add Employees</h1>
          </div>
          <div class="col-sm-3 ml-auto">
            <base-button type="primary" @click="newadd = !newadd">Back</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-sm-6 offset-sm-3">
            <card style="min-height: 200px;">
              <form @submit.prevent>
                <div>
                  <base-input
                    label="Enter First Name"
                    type="text"
                    placeholder="Enter First Name"
                    v-model="newemployee.fname"
                  >
                  </base-input>
                  <base-input
                    label="Enter Last Name"
                    type="text"
                    placeholder="Enter Last Name"
                    v-model="newemployee.lname"
                  >
                  </base-input>
                  <base-input
                    label="Enter Designation"
                    type="text"
                    placeholder="Enter Designation"
                    v-model="newemployee.designation"
                  >
                  </base-input>
                  <base-input
                    label="Enter Phone Number"
                    type="text"
                    placeholder="Enter Phone Number"
                    v-model="newemployee.phone"
                  >
                  </base-input>
                  <base-input
                    label="Choose Employee Type"
                  >
                    <el-select
                      class="select-primary"
                      size="large"
                      placeholder="Single Select"
                      v-model="newemployee.service_id"
                    >
                      <el-option
                        v-for="option in servicetypes"
                        class="select-primary"
                        :value="option.id"
                        :label="option.name"
                        :key="option.id"
                      >
                      </el-option>
                    </el-select>
                  </base-input>
                  <base-button class="mt-3" native-type="submit" type="primary" @click="createnew"
                  >Submit</base-button
                  >
                </div>
              </form>
            </card>
          </div>
        </div>
      </div>
      <div v-else class="padd">
        <div class="row">
          <div class="col-md-12 col-lg-8 mr-auto">
            <h1>All Employees</h1>
          </div>
          <div class="col-md-3 col-lg-2 ml-auto">
            <base-button type="primary" @click="updateservicetypes">Update Employeetypes</base-button>
          </div>
          <div class="col-md-3 col-lg-2 ml-auto">
            <base-button type="primary" @click="switchtoaddnew">Add New</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <template v-for="item in this.employees">
            <div v-bind:key="item.id" class="col-md-12 col-lg-6">
              <card style="min-height: 200px;">
                <div class="ml-auto mr-auto" style="text-align: justify;padding:40px;">
                  <tabs type="primary" vertical class="row">
                    <tab-pane label="Data">
                      <h1 class="card-subtitle" style="margin-top: 5px;">Name: &nbsp;{{item['fname']}}&nbsp;{{item['lname']}}</h1>
                      <h2 class="card-title" style="padding-top: 20px;padding-left: 0px;">Servicetype: &nbsp;{{item['servicetype']['name']}}</h2>
                      <h2 class="card-title" style="padding-top: 20px;padding-left: 0px;">Designation: &nbsp;{{item['designation']}}</h2>
                      <h2 class="card-title" style="padding-top: 20px;padding-left: 0px;">Phone: &nbsp;{{item['phone']}}</h2>
                    </tab-pane>
                    <tab-pane label="Update">
                      <form @submit.prevent>
                        <div>
                          <base-input
                            label="Enter Designation"
                            type="text"
                            placeholder="Enter Designation"
                            v-model="item.designation"
                          >
                          </base-input>
                          <base-input
                            label="Choose Service Type"
                          >
                            <el-select
                              class="select-primary"
                              size="large"
                              placeholder="Single Select"
                              v-model="item.service_id"
                            >
                              <el-option
                                v-for="option in servicetypes"
                                class="select-primary"
                                :value="option.id"
                                :label="option.name"
                                :key="option.id"
                              >
                              </el-option>
                            </el-select>
                          </base-input>
                          <base-button class="mt-3" native-type="submit" type="primary" @click="updateitem(item)"
                          >Submit</base-button
                          >
                        </div>
                      </form>
                    </tab-pane>
                    <tab-pane label="Open Profile">
                      <h2 class="card-title"><router-link :to="{ name: 'Employee', params:{id: item.id}}">Go to Profile</router-link></h2>
                    </tab-pane>
                  </tabs>
                </div>
                <div class="card-footer">
                  <hr style="background-color:rgba(255, 255, 255, 0.8);"/>
                  <div class="row">
                    <div class="col-md-3 mr-auto">
                      <h5>ID:  &nbsp;{{item.id}}</h5>
                    </div>
                    <div class="col-md-7 ml-auto">
                      <h5>Created At: &nbsp; {{item.createdAt}}</h5>
                    </div>
                  </div>
                </div>
              </card>
            </div>
          </template>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import swal from 'sweetalert2';
  import { Select, Option } from 'element-ui';
  import { BaseSwitch, TabPane, Tabs } from 'src/components';
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "manageemployee",
    components: {
      BaseSwitch,
      TabPane,
      [Option.name]: Option,
      [Select.name]: Select,
      Tabs,
      Loading
    },
    data: function () {
      return {
        isLoading: true,
        servicetypes: {},
        newadd: false,
        newemployee: {fname: '', lname: '', service_id: 0, designation: '', phone: '' },
        employees: {}
      }
    },
    methods: {
      updateservicetypes() {
        this.isLoading = true;
        this.$axios.get(this.$backend+'/api/customer/servicetypes')
          .then(res => {
            this.servicetypes = res.data;
            this.isLoading = false;
            swal({
              title: `Success!`,
              text: 'Employee Types updated successfully!',
              type: 'success',
              timer: 1000,
              showConfirmButton: false
            });
          }).catch(err => {
          swal({
            title: `Error!`,
            text: 'Employee Types could not be updated!',
            type: 'error',
            timer: 1500,
            showConfirmButton: false
          }).then(rr => {
            this.isLoading = false;
          });
        });
      },
      switchtoaddnew() {
        this.isLoading = true;
        this.$axios.get(this.$backend+'/api/customer/servicetypes')
          .then(res => {
            this.servicetypes = res.data;
            this.isLoading = false;
            this.newadd = true;
          }).catch(err => {
          this.isLoading = false;
        });
      },
      updateitem (payload) {
        let updatert = {
          id: payload.id,
          designation: payload.designation,
          service_id: payload.service_id
        }
        this.isLoading = true;
        this.$axios.put(this.$backend+'/api/admin/employee', updatert)
          .then(res => {
            this.$axios.get(this.$backend+'/api/admin/employees')
              .then(res => {
                this.employees = res.data;
                this.isLoading = false;
                swal({
                  title: `Success!`,
                  text: 'The Employee was updated successfully!',
                  type: 'success',
                  timer: 2000,
                  showConfirmButton: false
                });
              }).catch(err => {
              this.isLoading = false;
              swal({
                title: `Error!`,
                text: 'The Employee could not be updated!',
                buttonsStyling: false,
                confirmButtonClass: 'btn btn-success btn-fill',
                type: 'error'
              });
            });
          }).catch(err => {
          this.isLoading = false;
          swal({
            title: `Error!`,
            text: 'The Employee could not be updated!',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success btn-fill',
            type: 'error'
          });
        });
      },
      createnew () {
        this.isLoading = true;
        this.newadd = false;
        this.$axios.post(this.$backend+'/api/admin/employee', this.newemployee)
          .then(res => {
            let neew = res.data;
            this.$axios.get(this.$backend+'/api/admin/employees')
              .then(res => {
                this.employees = res.data;
                this.isLoading = false;
                swal({
                  title: `Success!`,
                  text: 'The Employee was added successfully! Password: ' + neew.password,
                  type: 'success',
                  timer: 4000,
                  showConfirmButton: false
                });
              });
          }).catch(err => {
          this.isLoading = false;
          swal({
            title: `Error!`,
            text: 'The Employee Type could not be added!',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success btn-fill',
            type: 'error'
          });
        });
      }
    },
    mounted() {
      this.$axios.get(this.$backend+'/api/admin/employees')
        .then(res => {
          this.employees = res.data;
          this.$axios.get(this.$backend+'/api/customer/servicetypes')
            .then(res => {
              this.servicetypes = res.data;
              this.isLoading = false;
            }).catch(err => {
            this.isLoading = false;
          });
        }).catch(err => {
        this.isLoading = false;
      });
    }
  }
</script>

<style scoped>
  .padd {
    padding-left: 40px;
    padding-right: 60px;
    padding-top: 15px;
    width: auto;
  }
</style>
