<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div v-if="newadd" class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>Add Customer</h1>
          </div>
          <div class="col-sm-3 ml-auto">
            <base-button type="primary" @click="newadd = !newadd">Back</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-sm-6 offset-sm-3">
            <card style="min-height: 200px;">
              <form @submit.prevent>
                <div>
                  <base-input
                    label="Enter First Name"
                    type="text"
                    placeholder="Enter First Name"
                    v-model="newcustomer.fname"
                  >
                  </base-input>
                  <base-input
                    label="Enter Last Name"
                    type="text"
                    placeholder="Enter Last Name"
                    v-model="newcustomer.lname"
                  >
                  </base-input>
                  <base-input
                    label="Enter Email"
                    type="email"
                    placeholder="Enter Email"
                    v-model="newcustomer.email"
                  >
                  </base-input>
                  <base-input
                    label="Enter ID Proof"
                    type="text"
                    placeholder="Enter ID Proof"
                    v-model="newcustomer.idproof"
                  >
                  </base-input>
                  <base-input
                    label="Enter Address"
                    type="text"
                    placeholder="Enter Address"
                    v-model="newcustomer.address"
                  >
                  </base-input>
                  <base-input
                    label="Phone Ext"
                    type="number"
                    placeholder="Phone Ext"
                    v-model="newcustomer.phone_ext"
                  >
                  </base-input>
                  <base-input
                    label="Enter Phone"
                    type="text"
                    placeholder="Enter Phone"
                    v-model="newcustomer.phone"
                  >
                  </base-input>
                  <base-button class="mt-3" native-type="submit" type="primary" @click="createnew"
                  >Submit</base-button
                  >
                </div>
              </form>
            </card>
          </div>
        </div>
      </div>
      <div v-else class="padd">
        <div class="row">
          <div class="col-sm-5 mr-auto">
            <h1>Customers</h1>
          </div>
          <div class="col-sm-2 ml-auto">
            <el-select
              class="select-primary mb-3 pagination-select"
              v-model="pagination.perPage"
              placeholder="Per page"
            >
              <el-option
                class="select-primary"
                v-for="item in pagination.perPageOptions"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </div>
          <div class="col-sm-3">
            <base-input>
              <el-input
                type="search"
                class="mb-3 search-input"
                clearable
                prefix-icon="el-icon-search"
                placeholder="Search Customers"
                v-model="searchQuery"
                aria-controls="datatables"
              >
              </el-input>
            </base-input>
          </div>
          <div class="col-sm-2">
            <base-button style="margin: 0!important;" type="primary" @click="newadd = !newadd">New</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row mt-5">
          <template  v-for="item in queriedData">
            <div v-bind:key="item.id" class="col-sm-12 col-md-6 col-xl-4" style="margin-bottom: 45px;">
              <card type="testimonial" header-classes="card-header-avatar">
                <a slot="header" href="#pablo">
                  <img class="img img-raised" src="img/james.jpg" alt="Card image" />
                </a>
                <h4 class="card-title">{{ item.fname }} &nbsp;{{ (item.lname === '' || item.lname === null) ? null : item.lname }}</h4>
                <a class="card-title" :href="'tel:+'+item.phone_ext+item.phone">Phone: +{{ item.phone_ext }}-{{ item.phone }}</a>
                <br/><a class="card-title" :href="'mailto:'+item.email">Email: {{ item.email }}</a>
                <div class="card-footer" style="padding-bottom: 0px; margin-bottom: 0px;">
                  <hr style="background-color:rgba(255, 255, 255, 0.8);"/>
                  <div class="row">
                    <div class="col-sm-3 col-md-3 mr-auto">
                      <h5>ID:  &nbsp;{{ item.id }}</h5>
                    </div>
                    <div class="col-sm-8 col-md-8 ml-auto">
                      <base-button type="primary btn-sm" style="margin: 0px;" @click="$router.push('/admin/customer/'+item.id)">Profile</base-button>
                    </div>
                  </div>
                </div>
              </card>
            </div>
          </template>
          <div
            class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap"
          >
            <div class="">
              <p class="card-category">
                Showing {{ from + 1 }} to {{ to }} of {{ total }} entries
              </p>
            </div>
            <base-pagination
              class="pagination-no-border"
              v-model="pagination.currentPage"
              :per-page="pagination.perPage"
              :total="total"
            >
            </base-pagination>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import Fuse from "fuse.js";
  import {Select, Option, Table, TableColumn} from 'element-ui';
  import { BasePagination } from 'src/components';
  import swal from "sweetalert2";
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "customers",
    components: {
      Loading,
      BasePagination,
      [Select.name]: Select,
      [Option.name]: Option
    },
    data: function () {
      return {
        isLoading: false,
        customers: '',
        pagination: {
          perPage: 6,
          currentPage: 1,
          perPageOptions: [2, 6, 18, 30],
          total: 0
        },
        newcustomer: {fname: '', lname: '', email: '', idproof: '', address: '', phone: '', phone_ext: 0 },
        searchQuery: '',
        propsToSearch: ['id', 'fname', 'lname', 'email', 'phone'],
        searchedData: [],
        fuseSearch: '',
        newadd: false
      }
    },
    computed: {
      queriedData() {
        let result = this.customers;
        if (this.searchedData.length > 0) {
          result = this.searchedData;
        }
        return result.slice(this.from, this.to);
      },
      to() {
        let highBound = this.from + this.pagination.perPage;
        if (this.total < highBound) {
          highBound = this.total;
        }
        return highBound;
      },
      from() {
        return this.pagination.perPage * (this.pagination.currentPage - 1);
      },
      total() {
        return this.searchedData.length > 0
          ? this.searchedData.length
          : this.customers.length;
      }
    },
    methods: {
      createnew () {
        this.isLoading = true;
        this.newadd = false;
        this.$axios.post(this.$backend+'/api/admin/customer', this.newcustomer)
          .then(res => {
            let neew = res.data;
            this.$axios.get(this.$backend+'/api/admin/customers')
              .then(res => {
                this.customers = res.data;
                this.fuseSearch = new Fuse(this.customers, {
                  keys: ['id', 'fname', 'lname', 'email', 'phone'],
                  threshold: 0.3
                });
                this.isLoading = false;
                swal({
                  title: `Success!`,
                  text: 'The Customer was added successfully! Password: ' + neew.password,
                  type: 'success',
                  timer: 4000,
                  showConfirmButton: false
                });
              });
          }).catch(err => {
          this.isLoading = false;
          swal({
            title: `Error!`,
            text: 'The Customer could not be added!',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success btn-fill',
            type: 'error'
          });
        });
      }
    },
    mounted() {
      this.$axios.get(this.$backend+'/api/admin/customers')
        .then(res => {
          this.customers = res.data;
          this.fuseSearch = new Fuse(this.customers, {
            keys: ['id', 'fname', 'lname', 'email', 'phone'],
            threshold: 0.3
          });
          this.isLoading = false;
        }).catch(err => {
          this.isLoading = false;
      });
    },
    watch: {
      searchQuery(value) {
        let result = this.customers;
        if (value !== '') {
          result = this.fuseSearch.search(this.searchQuery);
        }
        this.searchedData = result;
      }
    }
  }
</script>

