<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div v-if="newadd === 1" class="padd">
        <div class="row">
          <div class="col-sm-5 mr-auto">
            <h1>Choose Customer</h1>
          </div>
          <div class="col-sm-2 ml-auto">
            <el-select
              class="select-primary mb-3 pagination-select"
              v-model="pagination.perPage"
              placeholder="Per page"
            >
              <el-option
                class="select-primary"
                v-for="item in pagination.perPageOptions"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </div>
          <div class="col-sm-3">
            <base-input>
              <el-input
                type="search"
                class="mb-3 search-input"
                clearable
                prefix-icon="el-icon-search"
                placeholder="Search Customers"
                v-model="searchQuery"
                aria-controls="datatables"
              >
              </el-input>
            </base-input>
          </div>
          <div class="col-sm-2">
            <base-button type="primary" @click="changenewadd(0)">Back</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row mt-5">
          <template  v-for="item in queriedData">
            <div v-bind:key="item.id" class="col-sm-12 col-md-6 col-xl-4" style="margin-bottom: 45px;">
              <card type="testimonial" header-classes="card-header-avatar">
                <a slot="header" href="#pablo">
                  <img class="img img-raised" src="img/james.jpg" alt="Card image" />
                </a>
                <h4 class="card-title">{{ item.fname }} &nbsp;{{ (item.lname === '' || item.lname === null) ? null : item.lname }}</h4>
                <a class="card-title">Phone: +{{ item.phone_ext }}-{{ item.phone }}</a>
                <br/><a class="card-title">Email: {{ item.email }}</a>
                <div class="card-footer" style="padding-bottom: 0px; margin-bottom: 0px;">
                  <hr style="background-color:rgba(255, 255, 255, 0.8);"/>
                  <div class="row">
                    <div class="col-sm-3 col-md-3 mr-auto">
                      <h5>ID:  &nbsp;{{ item.id }}</h5>
                    </div>
                    <div class="col-sm-8 col-md-8 ml-auto">
                      <base-button type="primary btn-sm" style="margin: 0px;" @click="chooseCustomer(item)">Choose</base-button>
                    </div>
                  </div>
                </div>
              </card>
            </div>
          </template>
          <div
            class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap"
          >
            <div class="">
              <p class="card-category">
                Showing {{ from + 1 }} to {{ to }} of {{ total }} entries
              </p>
            </div>
            <base-pagination
              class="pagination-no-border"
              v-model="pagination.currentPage"
              :per-page="pagination.perPage"
              :total="total"
            >
            </base-pagination>
          </div>
        </div>
      </div>
      <div v-if="newadd === 2" class="padd">
        <div class="row">
          <div class="col-sm-5 mr-auto">
            <h1>Choose Room</h1>
          </div>
          <div class="col-sm-2">
            <base-button type="primary" @click="changenewadd(0)">Back</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-sm-12">
            <card>
              <el-table :data="availrooms.availablerooms">
                <el-table-column min-width="50" prop="id" label="ID">
                </el-table-column>
                <el-table-column min-width="150" prop="name" label="Room Name">
                </el-table-column>
                <el-table-column min-width="100" prop="floor" label="Room Floor">
                </el-table-column>
                <el-table-column min-width="150" prop="rtname" label="Room Type Name">
                </el-table-column>
                <el-table-column min-width="150" prop="price" label="Cost per night in Rs.">
                </el-table-column>
              <el-table-column
                min-width="150"
                header-align="right"
                align="right"
                label="Select"
              >
                <div
                  slot-scope="{ row, $index }"
                  class="text-right table-actions"
                >
                  <el-tooltip
                    content="Select"
                    effect="light"
                    :open-delay="300"
                    placement="top"
                  >
                    <base-button
                      :type="$index > 2 ? 'success' : 'neutral'"
                      icon
                      @click="selectroom(row)"
                      size="sm"
                      class="btn-link"
                    >
                      <i class="tim-icons icon-double-right"></i>
                    </base-button>
                  </el-tooltip>
                </div>
              </el-table-column>
            </el-table>
            </card>
          </div>
        </div>
      </div>
      <div v-else class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>New Booking</h1>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-sm-10 ml-auto mr-auto">
            <card style="min-height: 200px;">
              <form @submit.prevent>
                <div>
                  <div class="row">
                    <div class="col-sm-8">
                      <base-input
                        label="Enter Start Date"
                      >
                        <el-date-picker
                          type="date"
                          :disabled="datedisabler"
                          v-model="newbooking.startdate"
                          placeholder="Date Picker"
                        >
                        </el-date-picker>
                      </base-input>
                      <base-input
                        label="Enter End Date"
                      >
                        <el-date-picker
                          type="date"
                          :disabled="datedisabler"
                          v-model="newbooking.enddate"
                          placeholder="Date Picker"
                        >
                        </el-date-picker>
                      </base-input>
                      <base-input
                        label="Enter Number Of Guests"
                        type="number"
                        name="numguests"
                        v-validate="modelValidations.numguests"
                        :error="getError('numguests')"
                        placeholder="Enter Number Of Guests"
                        v-model="newbooking.numguests"
                      >
                      </base-input>
                    </div>
                    <div class="col-sm-4" style="padding-top:7px;">
                      <base-button :disabled="datebuttondisabler" class="mt-3" style="margin:0px;" type="primary" @click="checkBookable"
                      >Get Bookable Rooms</base-button>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-sm-8">
                      <base-input
                        label="Choose Customer"
                        type="text"
                        :disabled="true"
                        placeholder="Choose Customer"
                        v-model="newbooking.cust_id"
                      >
                      </base-input>
                    </div>
                    <div class="col-sm-4" style="padding-top:7px;">
                      <base-button class="mt-3" style="margin:0px;" type="primary" @click="changenewadd(1)"
                      >Choose Customer</base-button>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-sm-8">
                      <base-input
                        label="Choose Room"
                        type="text"
                        :disabled="true"
                        placeholder="Choose Room"
                        v-model="newbooking.room_id"
                      >
                      </base-input>
                    </div>
                    <div class="col-sm-4" style="padding-top:7px;">
                      <base-button class="mt-3" :disabled="roomdisabler" style="margin:0px;" type="primary" @click="changenewadd(2)"
                      >Choose Room</base-button>
                    </div>
                  </div>
                  <base-button class="mt-3" type="primary" @click="reset"
                  >Reset</base-button>&nbsp;&nbsp;
                  <base-button class="mt-3" native-type="submit" type="primary" @click.prevent="validate" @click="booknow"
                  >Submit</base-button>
                </div>
              </form>
            </card>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import Fuse from "fuse.js";
  import {Select, Option, Table, TableColumn, DatePicker} from 'element-ui';
  import { BasePagination } from 'src/components';
  import swal from "sweetalert2";
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "bookroom",
    components: {
      Loading,
      [Table.name]: Table,
      [TableColumn.name]: TableColumn,
      BasePagination,
      [DatePicker.name]: DatePicker,
      [Select.name]: Select,
      [Option.name]: Option
    },
    data: function () {
      return {
        modelValidations: {
          cust_id: {
            required: true
          },
          room_id: {
            required: true
          },
          startdate: {
            required: true
          },
          enddate: {
            required: true
          },
          numguests: {
            required: true,
            numeric: true
          }
        },
        inprogress: false,
        newbooking: {},
        availrooms: [],
        isLoading: true,
        customers: '',
        pagination: {
          perPage: 6,
          currentPage: 1,
          perPageOptions: [2, 6, 18, 30],
          total: 0
        },
        newcustomer: {fname: '', lname: '', email: '', idproof: '', address: '', phone: '', phone_ext: 0 },
        searchQuery: '',
        propsToSearch: ['id', 'fname', 'lname', 'email', 'phone'],
        searchedData: [],
        fuseSearch: '',
        newadd: 0
      }
    },
    computed: {
      roomdisabler () {
        if (this.availrooms.length !== 0)
        {
          return false;
        } else {
          return true;
        }
      },
      datedisabler () {
        if ('startdate' in this.newbooking && 'enddate' in this.newbooking )
        {
          return true;
        } else {
          return false;
        }
      },
      datebuttondisabler () {
        return this.datedisabler && this.inprogress === true
      },
      queriedData() {
        let result = this.customers;
        if (this.searchedData.length > 0) {
          result = this.searchedData;
        }
        return result.slice(this.from, this.to);
      },
      to() {
        let highBound = this.from + this.pagination.perPage;
        if (this.total < highBound) {
          highBound = this.total;
        }
        return highBound;
      },
      from() {
        return this.pagination.perPage * (this.pagination.currentPage - 1);
      },
      total() {
        return this.searchedData.length > 0
          ? this.searchedData.length
          : this.customers.length;
      }
    },
    methods: {
      selectroom (row) {
        console.log(row)
        this.newbooking.room_id = row.room_id;
        this.newadd = 0;
      },
      getError(fieldName) {
        return this.errors.first(fieldName);
      },
      validate() {
        this.$validator.validateAll().then(isValid => {
          this.$emit('on-submit', this.registerForm, isValid);
        });
      },
      reset () {
        this.newbooking = {};
        this.availrooms = [];
        this.inprogress = false;
      },
      booknow () {
        this.isLoading = true;
        this.$axios.post(this.$backend+'/api/customer/book', this.newbooking)
          .then(res => {
            this.isLoading = false;
            swal({
              title: `Success!`,
              text: 'Room booked',
              type: 'success',
              timer: 1500,
              showConfirmButton: true
            });
          }).catch(err => {
          swal({
            title: `Failure!`,
            text: 'Booking Failed!',
            type: 'error',
            timer: 3000,
          });
          this.isLoading = false;
        });

        this.newbooking = {};
        this.inprogress = false;
        this.availrooms = [];
      },
      checkBookable () {
        this.isLoading = true;
        this.inprogress = true;
        this.$axios.post(this.$backend+'/api/customer/checkbookable', this.newbooking)
          .then(res => {
            this.availrooms = res.data;
            console.log(this.availrooms)
            this.isLoading = false;
            swal({
              title: `Success!`,
              text: 'Bookable rooms retrieved',
              type: 'success',
              timer: 1500,
              showConfirmButton: true
            });
          }).catch(err => {
          swal({
            title: `Failure!`,
            text: 'No Bookable rooms found within the specified dates!',
            type: 'error',
            timer: 3000,
          });
          this.isLoading = false;
        });
      },
      changenewadd (value) {
        this.newadd = value;
      },
      chooseCustomer (item) {
        this.newbooking.cust_id = item.id;
        this.newadd = 0;
      },
      createnew () {
        this.isLoading = true;
        this.newadd = false;
        this.$axios.post(this.$backend+'/api/employees/customer', this.newcustomer)
          .then(res => {
            let neew = res.data;
            this.$axios.get(this.$backend+'/api/employees/customers')
              .then(res => {
                this.customers = res.data;
                this.fuseSearch = new Fuse(this.customers, {
                  keys: ['id', 'fname', 'lname', 'email', 'phone'],
                  threshold: 0.3
                });
                this.isLoading = false;
                swal({
                  title: `Success!`,
                  text: 'The Customer was added successfully! Password: ' + neew.password,
                  type: 'success',
                  timer: 4000,
                  showConfirmButton: false
                });
              });
          }).catch(err => {
          this.isLoading = false;
          swal({
            title: `Error!`,
            text: 'The Customer could not be added!',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success btn-fill',
            type: 'error'
          });
        });
      }
    },
    mounted() {
      this.$axios.get(this.$backend+'/api/employees/customers')
        .then(res => {
          this.customers = res.data;
          this.fuseSearch = new Fuse(this.customers, {
            keys: ['id', 'fname', 'lname', 'email', 'phone'],
            threshold: 0.3
          });
          this.isLoading = false;
        }).catch(err => {
        this.isLoading = false;
      });
    },
    watch: {
      searchQuery(value) {
        let result = this.customers;
        if (value !== '') {
          result = this.fuseSearch.search(this.searchQuery);
        }
        this.searchedData = result;
      }
    }
  }
</script>

