<template>
  <div>
    <Loading v-if="isLoading"/>
    <template v-else>
      <div v-if="newadd" class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>Add Payment Method</h1>
          </div>
          <div class="col-sm-3 ml-auto">
            <base-button type="primary" @click="newadd = !newadd">Back</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <div class="col-sm-6 offset-sm-3">
            <card style="min-height: 200px;">
              <form @submit.prevent>
                <div>
                  <base-input
                    label="Enter Card Number"
                    type="number"
                    placeholder="Enter Card Number"
                    v-model="newpaymentmethod.number"
                  >
                  </base-input>
                  <base-input
                    label="Enter Card Expiry"
                    type="date"
                    placeholder="Enter Price"
                    v-model="newpaymentmethod.expiry"
                  >
                  </base-input>
                  <base-button class="mt-3" native-type="submit" type="primary" @click="createnew"
                  >Submit</base-button
                  >
                </div>
              </form>
            </card>
          </div>
        </div>
      </div>
      <div v-else class="padd">
        <div class="row">
          <div class="col-sm-8 mr-auto">
            <h1>All Payment Methods</h1>
          </div>
          <div class="col-sm-3 ml-auto">
            <base-button type="primary" @click="newadd = !newadd">Add New</base-button>
          </div>
        </div>
        <hr style="background-color:rgba(255, 255, 255, 0.8); margin-bottom: 60px;"/>
        <div class="row">
          <template v-for="item in this.paymentmethods">
            <div v-bind:key="item.id" class="col-md-12 col-lg-4">
              <card style="min-height: 200px;">
                <div class="ml-auto mr-auto" style="text-align: justify;padding:40px;">
                  <tabs type="primary" vertical class="row">
                    <tab-pane label="Data">
                      <h1 class="card-subtitle" style="margin-top: 5px;">Name: &nbsp;{{item['number']}}</h1>
                      <h2 class="card-title" style="padding-top: 20px;padding-left: 0px;">Price: &nbsp;{{item['expiry']}}</h2>
                    </tab-pane>
                    <tab-pane label="Update">
                      <form @submit.prevent>
                        <div>
                          <base-input
                            label="Enter Card Number"
                            type="number"
                            placeholder="Enter Number"
                            v-model="item.number"
                          >
                          </base-input>
                          <base-input
                            label="Enter Expiry Date"
                            type="date"
                            placeholder="Enter Expiry"
                            v-model="item.expiry"
                          >
                          </base-input>
                          <base-button class="mt-3" native-type="submit" type="primary" @click="updateitem(item)"
                          >Submit</base-button
                          >
                        </div>
                      </form>
                    </tab-pane>
                  </tabs>
                </div>
                <div class="card-footer">
                  <hr style="background-color:rgba(255, 255, 255, 0.8);"/>
                  <div class="row">
                    <div class="col-md-3 mr-auto">
                      <h5>ID:  &nbsp;{{item.id}}</h5>
                    </div>
                    <div class="col-md-8 ml-auto">
                      <h5>Created At: &nbsp; {{item.createdAt}}</h5>
                    </div>
                  </div>
                </div>
              </card>
            </div>
          </template>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
  import swal from 'sweetalert2';
  import { TabPane, Tabs } from 'src/components';
  const Loading = () =>
    import(/* webpackChunkName: "extra" */ 'src/pages/Layout/LoadingMainPanel');
  export default {
    name: "paymentinfo",
    components: {
      TabPane,
      Tabs,
      Loading
    },
    data: function () {
      return {
        sd: {
          id: this.$store.state.userid
        },
        isLoading: true,
        paymentmethods: {},
        newadd: false,
        newpaymentmethod: {number: null, expiry: null}
      }
    },
    methods: {
      updateitem (payload) {
        let updatert = {
          id: payload.id,
          number: payload.number,
          expiry: payload.expiry
        }
        this.isLoading = true;
        this.$axios.put(this.$backend+'/api/customer/paymentinfo', updatert)
          .then(res => {
            this.$axios.post(this.$backend+'/api/customer/paymentinfo', this.sd)
              .then(res => {
                this.paymentmethods = res.data;
                this.isLoading = false;
                swal({
                  title: `Success!`,
                  text: 'The Payment Method was updated successfully!',
                  type: 'success',
                  timer: 2000,
                  showConfirmButton: false
                });
              });
          }).catch(err => {
          this.isLoading = false;
          swal({
            title: `Error!`,
            text: 'The Payment Method could not be updated!',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success btn-fill',
            type: 'error'
          });
        });
      },
      createnew () {
        this.isLoading = true;
        this.newadd = false;
        this.newpaymentmethod.cust_id = this.sd.id;
        this.$axios.post(this.$backend+'/api/customer/addpaymentinfo', this.newpaymentmethod)
          .then(res => {
            this.$axios.post(this.$backend+'/api/customer/paymentinfo', this.sd)
              .then(res => {
                this.paymentmethods = res.data;
                this.isLoading = false;
                swal({
                  title: `Success!`,
                  text: 'The Payment Method was added successfully!',
                  type: 'success',
                  timer: 2000,
                  showConfirmButton: false
                });
                this.newpaymentmethod = {}
              });
          }).catch(err => {
          this.isLoading = false;
          this.newpaymentmethod = {}
          swal({
            title: `Error!`,
            text: 'The Payment Method could not be added!',
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success btn-fill',
            type: 'error'
          });
        });
      }
    },
    mounted() {
      this.$axios.post(this.$backend+'/api/customer/paymentinfo', this.sd)
        .then(res => {
          this.paymentmethods = res.data;
          this.isLoading = false;
        });
    }
  }
</script>

<style scoped>
  .padd {
    padding-left: 40px;
    padding-right: 60px;
    padding-top: 15px;
    width: auto;
  }
</style>
